@media (max-width:768px) {
    header {
        width: 90%;
        margin: 20px auto;
        display: block;
        /* border: 1px solid white; */
    }

    header .headernav {
        position: relative;
        width: 100%;
        padding: 0;
        height: 68px;
        align-items: flex-start;
        justify-content: space-between;
        padding: 15px;
        /* transition: all 0.5s ease-in-out; */
    }
    header .headernav.active{
        height: 235px;
    }


    header .headernav .logo img {
        width: 110px;
    }

    header .headernav .navbar {
        position: absolute;
        top: 55px;
        width: 95%;
        /* border: 1px solid white; */
        display: none;
      
        /* transition: display 0.5s ease-in-out; */
    }

    header .headernav .activenav {
        display: block;
        transition: all 5s ease-in-out;
        /* transition-delay: 0.5s; */
    }

    header .headernav .navbar nav {
        transition: all 5s ease-in-out;
        /* transition-delay: 0.5s; */
        display: block;
        width: 100%;
        /* padding: 10px; */
    }
    header .headernav .navbar nav ul{
        display: block;
    }

    header .headernav .navbar nav ul .main .list {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 140px;
        margin: 8px 5px;
        height: 35px;
        border-radius: 30px;
        border: 1px solid var(--white);
        text-align: center;
        font-size: 16px;
        padding: 0;
        
        /* padding: 14px 0px; */
    }
    header .headernav .navbar nav ul .main .list:hover{
        border-radius:30px;
        background-color: transparent;
    }   
    header .headernav .navbar nav ul .navbox{
        display: flex;
        justify-content: center;
        align-items: center;
        /* margin: 20px auto; */
    }
    header .headernav .navbar nav ul .navbox .btnbookcall {
        width: 140px;
        margin: 8px 5px;
        height: 35px;
        border-radius: 30px;
        display: flex;
    }

    header .headernav .navbar nav .btnbookcall span {
        display: inline;
    }

    header .headernav .navbar nav .btnbookcall .icon-call {
        display: block;
        margin-left: 5px;
        color: red;
        font-size: 25px;
    }
   
    header .headernav .icon {
        display: flex;
        /* transition: all 0.5s ease-in-out; */
    }

    .menu {
        transition: transform 0.5s ease-in-out;
    }
    .menu.closed {
        /* transform: translateY(-100%); */
        /* opacity: 0; */
        pointer-events: none;
    }
    .menu.open {
        /* transform: translateY(0); */
        /* opacity: 1; */
        pointer-events: auto;
    }
}
@media (max-width:578px) {
    header .headernav {
        padding: 10px;
    }
}