header {
    width: 740px;
    height: auto;
    border-radius: 12px;
    margin: 20px auto;
    background-color: var(--headerbg);
    position: fixed;
    left: 0;
    right: 0;
    z-index: 5;
    backdrop-filter: blur(6px);
    border: 1px solid rgba(254, 114, 1, 0.2);
}

header .headernav {
    height: 75px;
    width: 100%;
    padding: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header .headernav .logo {
    margin: 0;
}

header .headernav .logo img {
    width: 120px;
}

header .headernav .navbar nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    padding: 0;
}

header .headernav .navbar nav ul {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
}

header .headernav .navbar nav ul .main .list {
    list-style: none;
    display: inline;
    color: var(--white);
    margin: 0px 3px;
    font-size: 14px;
    padding: 8px;
}

header .headernav .navbar nav ul .list:hover {
    border-radius: 5px;
    background-color: var(--graybg);
}
header .headernav .navbar nav ul .navbox{
    display: flex;
}
header .headernav .navbar nav ul .navbox .main{
    text-decoration: none;
    color: var(--white);
    display: flex;
    align-items: center;
}

header .headernav .navbar nav ul .navbox .btnbookcall {
    /* display: inline; */
    justify-content: center;
    align-items: center;
    background-color: var(--orange);
    border: none;
    border-radius: 4px;
    width: 140px;
    height: 45px;
    margin-left: 12px;
    text-decoration: none;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 12px 20px;
}

header .headernav .navbar nav .btnbookcall:hover {
    box-shadow: 0px 0px 8px 0px var(--orange);

}

header .headernav .navbar nav .btnbookcall p {
    list-style: none;
    display: inline;
    color: var(--white);
    /* margin: 0px 5px; */
    font-size: 16px;
    margin-bottom: 0px;
   
}


header .headernav .navbar nav .btnbookcall .icon-call {
    display: none;
}

header .headernav .icon {
    width: 28px;
    height: 28px;
    justify-content: center;
    align-items: center;
    display: none;
    margin-top: 7px;
    /* border: 1px solid white; */
}

header .headernav .icon .icon-bars {
    /* color: var(--white); */
    font-size: 40px;
    font-weight: 100;
    color: #FFF5EDFF;
}