
footer .firstfooter{
    border-top: 1px solid var(--graybg);
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
footer .firstfooter .image{
    margin-left: 10px;
}
footer .firstfooter .image img{
    width: 140px;
}
footer .firstfooter .social{
    width: 250px;
    display: flex;
}

footer .firstfooter .social span{
    color: var(--white);
    font-size: 16px;
    margin: 0;
    width: 100px;
}
footer .firstfooter .social .icons{
    width: 170px;
    display: flex;
    justify-content: space-around;
    margin-right: 10px;
    align-items: center;
}

footer .firstfooter .social .icons .icon{
    font-size: 20px;
    color: var(--white);
}
footer .secondfooter{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
footer .secondfooter .footertxt{
    color: white;
    text-align: center;
    margin: 20px auto;
    font-size: 14px;
    padding: 0px 15px;
}

@media (max-width:640px) {
    footer .firstfooter{
        height: 90px;
    }
    footer .firstfooter .social {
        width: 200px;
    }
    footer .firstfooter .image img {
        width: 130px;
    }
    footer .secondfooter .footertxt{
        margin: 10px auto;
    }
}
@media (max-width:380px) {
    footer .firstfooter .image img {
        width: 100px;
    }
    footer .firstfooter .social {
        width: 180px;
    }
    footer .firstfooter .social span {
        font-size: 14px;
    }
}
