/* AnimatedText.module.css */
@font-face {
    font-family: titleFont;
    src: url('../RG-StandardBold.ttf');
}

.container {
    /* margin-top: 20px; */
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.text {
    font-size: 54px;
    font-family: titleFont;
    font-weight: 600;
    color: var(--white);
    opacity: 0;
    transform: translateY(20px);
    animation-fill-mode: forwards;
    padding-right: 7px;
    line-height: 55px;

}

.container.animate .a {
    animation: slideUp 0.5s ease-out 0.1s forwards;
}

.container.animate .b {
    animation: slideUp 0.5s ease-out 0.2s forwards;
}

.container.animate .c {
    animation: slideUp 0.5s ease-out 0.3s forwards;
}

.container.animate .d {
    animation: slideUp 0.5s ease-out 0.4s forwards;
}

.container.animate .e {
    animation: slideUp 0.5s ease-out 0.5s forwards;
}

.container.animate .f {
    animation: slideUp 0.5s ease-out 0.6s forwards;
}

.container.animate .g {
    animation: slideUp 0.5s ease-out 0.7s forwards;
}

.container.animate .h {
    animation: slideUp 0.5s ease-out 0.8s forwards;
}

.container.animate .i {
    animation: slideUp 0.5s ease-out 0.9s forwards;
}

.container.animate .j {
    animation: slideUp 0.5s ease-out 1s forwards;
}



@keyframes slideUp {
    0% {
        opacity: 0;
        transform: translateY(20px);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@media (max-width:820px) {
    .container{
        max-width: 100%;
        /* justify-content: left; */
        margin: 20px 0px 0px;
        padding: 0;
    }
    .text {
        font-size: 50px;
        line-height: 55px;
    }
}

@media (max-width:640px) {
   .container{
    max-width: 300px;
    margin: 10px auto;
   }
    .text {
        font-size: 30px;
        line-height: 34px;
        font-weight: 500;
    }
}