.text-box {
    margin: 5px auto 10px;
    height: auto;
    max-width: 400px;
    display: flex;
    justify-content: center;
}

.text-box .star {
    color: var(--orange);
    margin-top: 5px;
}
.text-box .icon_orange{
    width: 15px;
    height: 16px;
    margin-top: 4px;
    animation: iconrotation 10s infinite ease-in;
    filter: opacity(1);
    filter: brightness(1);
}
.icon_orange{
    filter: opacity(1);
    filter: brightness(1);
    width: 15px;
    height: 16px;
    margin-top: 4px;
    animation: iconrotation 2s infinite ease-in;
}
@keyframes iconrotation {
    0%{
        transform: rotate(50deg);
    }
    20%{
        transform: rotate(100deg);
    }
    40%{
        transform: rotate(150deg);
    }
    60%{
        transform: rotate(200deg);
    }
    80%{
        transform: rotate(280deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.text-box p {
    color: #FE7201;
    margin-left: 8px;
    font-size: 16px;
    margin-bottom: 0;
    margin-top: 0;
    font-weight: 400;
}
h1{
    width: 100%;
    height: auto;
    text-align: center;
    margin-top: 15px;
    color: var(--white);
    margin: 10px auto;
    text-align: center;
