/* first section */
#first{
    border: 1px solid transparent;
    min-height: 500px;
    width: 100%;
    position: relative;
}
#first .shadowbox{
    width: 100%;
    height: 600px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    background: radial-gradient(45% 65% at 2% 2%, #FE7201 0%, rgba(0, 18, 0, 0) 60.76011863914697%, rgba(13, 12, 12, 0) 100%);
    background-repeat: no-repeat;

}
#first .first-container {
    height: auto;
    border-radius: 30px;
    background-color: var(--headerbg);
    margin-top: 150px;
    padding-top: 40px;
}
#first .first-container .row{
    margin: 0;
}
#first .first-container .row .box{
    width: 700px;
    height: auto;
    margin: 20px auto;
}
#first .first-container .row .box .para{
    text-align: center;
    color: var(--white);
    margin: 20px auto;
    font-size: 16px;
    font-weight: 300;
}
#first .first-container .row .box .btnbox{
    width: 90%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px auto;
}
#first .first-container .row .box .btnbox .btn .btnp{
    color: var(--white);
    text-decoration: none;
    margin-bottom: 0;
}
#first .first-container .row .box .btnbox .btn{
    width: 230px;
    height: 48px;
    border: none;
    background-color: var(--graybg);
    border-radius: 4px;
    border: 1px solid var(--btnborder);
    color: var(--white);
    font-size: 16px;
    cursor: pointer;
    margin-left: 10px;
    position: relative;
    overflow: hidden;

}
#first .first-container .row .box .btnbox .btn:hover{
    box-shadow: 0px 0px 10px 0px var(--btnborder);
    transition: box-shadow 0.3s ease-in;
}

#first .first-container .row .box .btnbox .b1{
    background-color: var(--orange);
    border: none;
}
#first .first-container .row .box .btnbox .b1:hover{
